  height: 100%;
  }
  
  .icon-play {
  background-image: url(./hamburger.png);
  background-size: cover;
  display: inline-block;
  margin-top: 8px;
  margin-left: 0px;
  margin-right: 10px;
  height: 20px;
  width: 20px;
  }
  
  .dropdown-toggle::after {
  display: none;
  }
  
  .divider:after,
  .divider:before {
  content: "";
  flex: 1;
  height: 1px;
  background: black;
  }
  
  .h-custom {
  height: calc(100% - 73px);
  }
  
  /* NEW AUTOCOMPLERE USING TYPEHEAD */
  
  body {
  margin: 30px;
  }
  
  .form-control {
  padding-right: 34px;
  }
  
  .toggle-button {
  background: transparent;
  border: 0;
  bottom: 0;
  padding: 0 12px;
  position: absolute;
  right: 0;
  top: 0;
  }
  
  .div-table {
  /* border: 1px solid red; */
  text-align: center;
  }
  
  table {
  /* border: 1px solid blue; */
  display: inline-table;
  /* outline-color: white; */
  /* outline-style: solid; */
  background: white;
  
  }
  
  td {
  text-align: left;
  }
  
  span {
  font-weight: bolder;
  font-style: italic;
  }
  
  .rating-avg {
  /* color: blue; */
  font-size:78%;
  background: white;
  }
  
  .rate-comment {
  color: rgb(172, 70, 70);
  }
  
  .delete-button {
  font-size: 80%;
  /* display: flex;
  justify-content: flex-end; */
  /* margin-right: 0; */
  }
  
  .date-time {
  /* color: blue; */
  font-size: 78%;
  
  }
  
  .city-separator {
  background-color: black;
  color: white;
  }
  
  .box {
  border:1px solid white;
  color:rgb(36, 36, 49);
  }
  
  .box:nth-last-child(even) {
  background: rgb(230, 235, 232);
  }
  
  .box:nth-last-child(odd) {
  background: rgb(255, 231, 204);
  }
  
  
  
  
  
  
  
  
  
  
  .App {
  text-align: center;
  }
  
  .App-logo {
  height: 40vmin;
  pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
  .App-logo {
  animation: App-logo-spin infinite 20s linear;
  }
  }
  
  .App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  }
  
  .App-link {
  color: #61dafb;
  }
  
  @keyframes App-logo-spin {
  from {
  transform: rotate(0deg);
  }
  to {
  transform: rotate(360deg);
  }
  }
  